@import 'assets/scss/variables.scss';

.wrapper {
    height: 100%;
    & .container {
        max-width: 1128px;
        margin: auto;
        background-color: $light;
        height: 100%;
        & .image-wrapper, & .details-wrapper {
            padding: 2rem;
            color: $dark;
        }
        & .image-wrapper {
            width: 40%;
            & .image {
                padding: 1.5rem;
                border: 1px solid rgba($grayTwo, 0.5);
                & img {
                    width: 100%;
                }
            }
            & .button-wrapper {
                gap: 1rem;
                margin-top: 1rem;
                & .resume-button, & .hire-button {
                    display: flex;
                    align-items: center;
                    border-radius: 5px;
                    justify-content: center;
                    width: 100%;
                    padding: 1.35rem 0;
                    color: $light;
                    font-size: 1.25rem;
                    text-transform: uppercase;
                    font-weight: 500;
                    & svg {
                        height: 1.5rem;
                        width: 1.5rem;
                        fill: $light;
                        margin-right: 0.5rem;
                        fill: $light;
                    }
                }
                & .resume-button {
                    background-color: $flipkartYellowTwo;
                }
                & .hire-button {
                    background-color: $flipkartOrange;
                }
            }
        }
        & .details-wrapper {
            width: 60%;
            height: calc(100vh - 13.5rem);
            overflow: auto;
            scrollbar-width: none;
            -ms-overflow-style: none;
            &::-webkit-scrollbar {
                width: 0;
                background: transparent;
            }
            & .breadcrumb {
                color: $gray;
                & li {
                    padding-right: 0.5rem;
                    &::after {
                        content: '>';
                        display: inline-block;
                        padding-left: 0.5rem;
                    }
                    &:last-child {
                        &::after {
                            display: none;
                        }
                    }
                }
            }
            & .title {
                font-size: $textLarge + 0.25rem;
                margin: 1.5rem 0 1rem;
                font-weight: 400;
            }
            & .rating {
                background-color: $success;
                padding: 0.25rem;
                border-radius: 3.5px;
                width: fit-content;
                color: $light;
                margin-right: 1rem;
                & svg {
                    height: 1rem;
                    width: 1rem;
                    margin-left: 0.25rem;
                    fill: $light;
                }
            }
            & .testimonials-wrapper {
                margin-bottom: 1rem;
                & p {
                    color: $gray;
                }
            }
            & .designation {
                color: $success;
                font-weight: 500;
                font-size: $textPrimary;
            }
            & .list {
                margin-top: 2.5rem;
                & li {
                    margin-bottom: 1rem;
                    font-size: $textPrimary - 0.25rem;
                    & svg {
                        height: 1.25rem;
                        width: 1.25rem;
                        margin-right: 0.5rem;
                        fill: $success;
                        transform: scaleX(-1);
                    }
                }
            }
            & .key-points {
                margin-top: 2.5rem;
                & .label {
                    color: $gray;
                    width: 7.5rem;
                }
                & .information {
                    margin-bottom: 1.5rem;
                    & .pin {
                        border-bottom: 2px solid $flipkartBlue;
                        padding-bottom: 0.75rem;
                        font-size: $textSmall + 0.05;
                        line-height: 1;
                        width: fit-content;
                        & svg {
                            height: 1.5rem;
                            width: 1.5rem;
                            margin-right: 0.5rem;
                            fill: $flipkartBlue;
                        }
                    }
                    & .join-date {
                        font-weight: 500;
                        font-size: $textSmall - 0.05;
                        margin-top: 1rem;
                        & small {
                            display: block;
                            font-weight: 300;
                            margin-top: 0.15rem;
                        }
                        & span {
                            color: $success;
                        }
                    }
                }
                & .email, & .number {
                    font-size: $textPrimary;
                    & svg {
                        height: 1.5rem;
                        width: 1.5rem;
                        margin-right: 1rem;
                    }
                }
            }
            & .description, & .review {
                padding: 1.5rem 2rem;
                border: 1px solid rgba($grayTwo, 0.25);
                margin-bottom: 2rem;
                & h5 {
                    font-size: $textMedium;
                    font-weight: 400;
                    margin-bottom: 1rem;
                    border-bottom: 1px solid rgba($grayTwo, 0.25);
                    padding-bottom: 1rem;
                }
                & p {
                    font-size: $textSmall;
                    color: rgba($darkShadeOne, 0.85);
                    font-weight: 300;
                    line-height: 1.5;
                }
            }
            & .review {
                & .testmonial-item {
                    border-bottom: 1px solid rgba($grayTwo, 0.25);
                    padding-bottom: 1rem;
                    margin-bottom: 1rem;
                    & .user-info {
                        margin-top: 1rem;
                        & .dp {
                            height: 2rem;
                            width: 2rem;
                            border-radius: 50%;
                            border: 1px solid rgba($grayTwo, 0.25);
                        }
                        & h4 {
                            font-weight: 400;
                            margin-left: 1rem;
                            opacity: 0.7;
                        }
                    }
                    & .message {
                        font-size: $textPrimary;
                        margin-top: 1rem;
                    }
                }
            }
        }
    }
}