@import 'assets/scss/variables.scss';

.wrapper {
    position: relative;
    font-family: $primaryFont;
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    & .dropdown {
        position: absolute;
        top: 0;
        right: 0;
        height: 2.4rem;
        & svg {
            height: 1rem;
            width: 1rem;
        }
    }
    :global(.linkedin-header), :global(.google-header) {
        & :global(.side-panel-global) {
            background-color: $light !important;
            & header {
                padding: 1.855rem 2rem !important;
                & svg {
                    fill: $danger !important;
                }
            }
            & ul {
                & li {
                    color: $dark !important;
                    &:hover {
                        color: $dark !important;
                    }
                }
            }
        }
    }
    :global(.google-header)  {
        & header {
            font-family: $tertiaryFont;
            color: $dark;
            font-size: $textModerate;
        }
        & ul {
            & li {
                font-family: $tertiaryFont;
                font-weight: 400;
            }
        }
    }
    :global(.flipkart-header) {
        & :global(.side-panel-global) {
            background-color: $light;
            & header {
                background-color: $flipkartBlue;
                padding: 2.375rem 2rem;
                font-size: $textModerate;
                font-weight: 400;
                & svg {
                    background-color: $dangerTwo;
                    border-radius: 1rem;
                    padding: 0.5rem;
                    fill: $light;
                }
            }
            & ul {
                & li {
                    color: $dark;
                    font-size: $textModerate - 0.1rem;
                    & svg {
                        height: 1.25rem;
                        width: 1.25rem;
                    }
                    &:hover {
                        color: $dark;
                    }
                }
            }
            & :global(.theme-msg-global) {
                background-color: rgba($flipkartYellow, 0.25);
                border: 1px solid $flipkartYellow;
            }
        }
    }
}