@import 'assets/scss/variables.scss';

.wrapper {
    background-color: $light;
    box-shadow: 0 1px 1px 0 rgba($darkShadeEight,.16);
    z-index: 1;
    & .container {
        max-width: 1128px;
        padding: 0.75rem 2.5rem;
        margin: auto;
        & .nav-links {
            & li {
                color: $dark;
                font-weight: 500;
            }
        }
    }
}