@import 'assets/scss/variables.scss';

.container {
    height: 100%;
    & .terminal {
        height: 100%;
        background-color: $darkShadeFour;
        border-top: 1px solid rgba($gray, 0.25);
        border-left: 1px solid rgba($gray, 0.25);
        padding: 2.5rem;
        max-height: 14.25rem;
        & input {
            width: 100%;
            margin-top: -2.5rem;
            background-color: transparent;
            color: $light;
            padding: 2.5rem 0 1rem 1rem;
            border: 0;
            font-size: $textPrimary;
            &:focus {
                outline: none;
            }
            &::placeholder {
                color: rgba($gray, 0.5);
                opacity: 0.5;
            }
        }
        & p {
            min-width: fit-content;
            color: rgba($light, 0.5);
            font-size: $textPrimary;
        }
        & h3 {
            font-size: $textMedium - 0.25rem;
            font-weight: 200;
            margin-bottom: 1rem;
            border-bottom: 1px solid rgba($light, 0.15);
            width: fit-content;
            padding: 0 0.35rem 0.5rem;
        }
        & .alert {
            font-size: $textPrimary;
            &.success {
                color: $successTwo;
            }
            &.error {
                color: $dangerTwo;
            }
            & div {
                font-size: $textSmall;
                color: $light;
                margin-top: 1rem;
                & span {
                    color: $googleBlue;
                }
            }
        }
    }
}