@import 'assets/scss/variables.scss';

.wrapper {
    font-size: $textMedium;
    & .class-name {
        color: rgba($flipkartYellowTwo, 0.75);
        text-transform: lowercase;
        & span {
            color: $flipkartYellow;
        }
    }
    & .end-brackets {
        color: $flipkartYellow;
    }
    & ul {
        margin-left: 2.75rem;
        color: $code;
        & span {
            color: $light;
            &:after {
                content: ";";
                color: $lightDanger;
                padding-left: 0.25rem;
            }
            &:before {
                content: ":";
                margin-right: 0.5rem;
                color: $light;
            }
        }
        & .with-quotes {
            color: $gray;
        }
    }
}