@import 'assets/scss/variables.scss';

.wrapper {
    background-color: $darkShadeThree;
    padding-top: 3.5rem;
    max-height: calc(100% - 22.75rem);
    border-left: 1px solid rgba($gray, 0.25);
    & .introduction {
        height: calc(100% + 3.5rem);
        & .logo {
            margin-bottom: 3rem;
        }
        & .comment {
            margin-bottom: 3rem;
        }
        & .heading {
            margin-bottom: 2rem;
        }
        & .designation, .about-description {
            font-family: $secondaryFont;
            font-size: $textPrimary;
        }
        & .designation {
            color: $code;
        }
    }
}