@import 'assets/scss/variables.scss';

.wrapper {
    background-color: $darkShadeFive;
    padding: 1rem 0;
    text-align: center;
    font-family: $secondaryFont;
    position: relative;
    display: flex;
    justify-content: center;
    @include md {
        display: flex;
        justify-content: space-between;
        padding-left: 2rem;
        & li {
            &:first-child {
                display: none;
            }
            &:nth-child(2) {
                border-left: 0;
            }
        }
    }
    & .socialmedia {
        display: none;
        @include md {
            display: block;
        }
    }
    & .logo {
        & p {
            line-height: 0.6;
            color: rgba($light, 0.5);
            font-size: $textModerate;
            @include md {
                font-size: $textPrimary;
            }
        }
        & svg {
            height: 2rem;
            width: 2rem;
            display: none;
            margin-right: 0.5rem;
            fill: rgba($light, 0.5);
            @include md {
                display: block;
            }
        }
    }
    @include md {
        background-color: $darkShadeSix;
    }
    & .tooltip {
        position: absolute;
        right: 0;
        top: 0;
        width: 20rem;
        padding: 20px;
        background: $darkShadeThree;
        color: $grayThree;
        box-shadow: rgba($darkShadeNine, 0.5) -5px 5px 0px -3px;
        margin: 1.5rem 6rem 0 0;
        border-radius: 1rem 0 1rem 1rem;
        text-align: left;
        font-family: $primaryFont;
        line-height: 1.35;
        opacity: 0; 
        transform: scale(0.2);
        &.show {
            opacity: 1;
            transform: scale(1);
        }
        @include lg {
            display: none;
        }
        & img {
            position: absolute;
            top: -1.25rem;
            width: 2.5rem;
            right: -2.75rem;
            height: 2.5rem;
        }
        &:after {
            content: "";
            display: block;
            width: 0;
            height: 0;
            border-style: solid;
            position: absolute;
            top: 0px;
            border-color: $darkShadeThree;
            border-width: 0 0 24px 20px;
            border-top-color: transparent !important;
            border-right-color: transparent !important;
            border-bottom-color: transparent !important;
            right: -20px;
        }
    }
    & .theme-button {
        position: absolute;
        right: 0;
        background: transparent;
        height: 1.5rem;
        border: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        color: $light;
        cursor: pointer;
    }
}