@import 'assets/scss/variables.scss';

.wrapper {
    height: fit-content;
    gap: 2.5rem;
    flex-wrap: wrap;
    & .frame-wrapper {
        font-family: $primaryFont;
        width: calc(50% - 2.5rem);
        margin-bottom: 2.5rem;
        & .frame {
            height: auto;
            background-color: $grayThree;
            border: 0.75rem solid $extraDark;
            padding: 0.75rem;
            box-shadow: 0.75rem 0.75rem 2rem 0 rgba($darkShadeTwo , 0.35), inset 1px 1px 5px 0px rgba($darkShadeNine , 0.5);
            margin-bottom: 1rem;
            & img {
                width: 100%;
                border: 0.01rem solid rgba($extraDark, .25);
            }
            & :global(.skeleton-loader-global) {
                height: 220px;
            }
        }
        & h2 {
            font-weight: 400;
            margin-bottom: 1rem;
            font-size: $textMedium;
        }
        & p {
            color: $gray;
            font-weight: 300;
            font-size: $textSmall;
            padding-bottom: 1rem;
            border-bottom: 1px solid rgba($light, 0.075);
        }
        & .actions-wrapper {
            gap: 1.5rem;
            & .actions {
                margin-top: 1rem;
                font-size: $textModerate;
                & svg {
                    &:first-child {
                        height: 1.75rem;
                        width: 1.75rem;
                        margin-right: 1rem;
                    }
                    &:last-child {
                        fill: $light;
                        height: 1.25rem;
                        width: 1.25rem;
                        opacity: 0;
                        transition: all ease-in-out 0.5s;
                        margin-left: 0.5rem;
                        transform: translateX(-5px);
                    }
                }
                & a {
                    color: $light;
                    text-decoration: none;
                }
                &:hover {
                    & a {
                        color: $info;
                    }
                    & svg {
                        &:last-child {
                            opacity: 0.75;
                            transform: translateX(0px);
                        }
                    }
                }
            }
        }
    }
}